import { RouterModule, Routes } from '@angular/router';
	
import { LoginComponent } from './login/login.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ShippingComponent } from './shipping/shipping.component';
import { GeneralComponent } from './shipping/general/general.component';
import { DocumentComponent } from './shipping/document/document.component';
import { PaymentComponent } from './shipping/payment/payment.component';
import { CmrComponent } from './shipping/cmr/cmr.component';
import { NotesComponent } from './shipping/notes/notes.component';
import { TransportComponent } from './shipping/transport/transport.component';
import { ContractsComponent } from './contracts/contracts.component';
import { TsclientComponent } from './contracts/tsclient/tsclient.component';
import { TsforwarderComponent } from './contracts/tsforwarder/tsforwarder.component';
import { MmclientComponent } from './contracts/mmclient/mmclient.component';
import { MmforwarderComponent } from './contracts/mmforwarder/mmforwarder.component';

import { EditComponent } from './contracts/edit/edit.component';


const routes: Routes = [
    { path: '', redirectTo:'/login', pathMatch: 'full' },
    { path: 'login', component: LoginComponent },
    { path: 'dashboard', component: DashboardComponent },
    { path: 'contracts', component: ContractsComponent,children:[
     	{path:'tsclient', component: TsclientComponent},
    	{path:'tsforwarder', component: TsforwarderComponent},
    	{path:'mmclient', component: MmclientComponent},
        {path:'mmforwarder', component: MmforwarderComponent},
    	{path:'edit/:id', component: EditComponent}
    ]},
    { path: 'shipping/:id', component: ShippingComponent,children:[
        {path:'general', component: GeneralComponent},
        {path:'transports', component: TransportComponent},
        {path:'documents', component: DocumentComponent},
        {path:'payments', component: PaymentComponent},
        {path:'cmr', component: CmrComponent},
        {path:'notes', component: NotesComponent}
    ]},
    // otherwise redirect to home
    { path: '**', redirectTo: '' }
];

export const appRoutingModule = RouterModule.forRoot(routes);